<template>
    <div>
        <el-main>
            <div class="select">
                <el-date-picker
                    v-model="time"
                    type="daterange"
                    range-separator="-"
                    start-placeholder="日期"
                    end-placeholder
                ></el-date-picker>
                <el-input v-model="ip" placeholder="IP" clearable></el-input>
                <el-input v-model="name" placeholder="名字" clearable></el-input>
                <el-button type="primary" icon="el-icon-search" class="search"></el-button>
            </div>
            <div class="btn">
                <el-button type="primary" icon="el-icon-document" @click="exportExcel()">导出</el-button>
            </div>
            <el-table
                id="out-table"
                border
                ref="multipleTable"
                tooltip-effect="dark"
                style="width: 100%"
                @selection-change="handleSelectionChange"
                :header-cell-style="{background:'#f5f7fa'}"
                :default-sort="{prop: 'date', order: 'descending'}"
                :data="tableData.filter(data => !search || data.name.toLowerCase().includes(search.toLowerCase()))"
            >
                <el-table-column type="selection" width="55"></el-table-column>
                <el-table-column prop="date" label="日期" sortable></el-table-column>
                <el-table-column prop="name" label="姓名"></el-table-column>
                <el-table-column prop="ip" label="ip地址"></el-table-column>
                <el-table-column prop="action" label="行为"></el-table-column>
            </el-table>
            <el-pagination
                style="float: right;margin-top:8px;"
                background
                layout="prev, pager, next"
                :total="1000"
            ></el-pagination>
        </el-main>
    </div>
</template>

<script>
import FileSaver from "file-saver";
import XLSX from "xlsx";
export default {
    data() {
        return {
            ip: "",
            time: "",
            name: "",
            tableData: [
                {
                    date: "2019-01-01",
                    name: "admin",
                    ip: "127.0.0.1",
                    action: "修改员工资料"
                },
                {
                    date: "2019-02-02",
                    name: "tset",
                    ip: "127.123.202.111",
                    action: "删除员工"
                },
                {
                    date: "2017-05-21",
                    name: "admin",
                    ip: "127.10.10.11",
                    action: "添加员工"
                },
                {
                    date: "2016-11-11",
                    name: "admin",
                    ip: "127.0.0.1",
                    action: "修改管理员权限"
                },
                {
                    date: "2017-10-01",
                    name: "abc",
                    ip: "12.10.10.221",
                    action: "修改员工资料"
                },
                {
                    date: "2018-11-11",
                    name: "admin",
                    ip: "126.200.200.100",
                    action: "修改密码"
                }
            ]
        };
    },
    methods: {
        handleSelectionChange(val) {
            this.multipleSelection = val;
        },
        exportExcel() {
            var wb = XLSX.utils.table_to_book(
                document.querySelector("#out-table")
            );
            var wbout = XLSX.write(wb, {
                bookType: "xlsx",
                bookSST: true,
                type: "array"
            });
            try {
                FileSaver.saveAs(
                    new Blob([wbout], { type: "application/octet-stream" }),
                    "log.xlsx"
                );
            } catch (e) {
                if (typeof console !== "undefined") console.log(e, wbout);
            }
            return wbout;
        }
    }
};
</script>

<style lang="stylus" scoped>
body {
    margin: 0;
}

.btn {
    margin-top: 10px;
    margin-bottom: 10px;
}

.select {
    display: inline;
}

.select, .el-input {
    width: 156px;
}
.search {
    position: relative;
    top: -1px;
}
</style>


